<template>
    <div class="blog-main" style="min-height: 600px" v-loading="loading">
        <el-card shadow="never" style="margin-bottom: 20px">
            <el-select @change="select" v-model="selectValue" placeholder="请选择分类" style="padding-right: 12px;">
                <el-option-group v-for="group in types" :key="group.label" :label="group.label">
                    <el-option v-for="item in group.options" :key="item.value" :label="item.label" :value="item.value"></el-option>
                </el-option-group>
            </el-select>
            <el-input @change="search" prefix-icon="el-icon-search" placeholder="请输入关键字" v-model="searchKey" clearable style="width: 200px"></el-input>
            <el-button @click="$share()" style="margin-left: 10px; float: right;" icon="el-icon-share" type="warning" plain circle></el-button>
            <el-button @click="goAdd" type="primary" icon="el-icon-edit" round plain style="float: right;">写博文</el-button>
        </el-card>

        <div v-if="blogs&&blogs.length>0">
            <el-card shadow="hover" v-for="(item,index) in blogs" :key="'p'+index" style="margin-bottom: 20px" v-if="!item.hide">
                <div slot="header">
                    <span>
                        <a style="text-decoration:none;cursor:pointer" @click="goDetails(item.id)">
                            <i class="el-icon-edit-outline"></i>&nbsp;&nbsp; <span style="font-size: 1.5rem;">{{item.title}}</span>
                        </a>
                    </span>
                </div>
                <el-row>
                    <el-col :span="14">
                        <div>
                            <el-tag type="danger" style="margin-right: 10px;" size="mini"> {{item.type}} </el-tag>
                            <el-tag v-for="(tag, index) in item.tags" style="margin-right: 10px;" size="mini" :key="index"> {{tag}} </el-tag>
                        </div>
                    </el-col>
                    <el-col :span="10">
                        <div style="text-align: right; float: right">
                            <el-button @click="$share('/user/blog/details/'+item.id)" style="padding: 3px 0" type="text" icon="el-icon-share"></el-button>
                            <el-button @click="editBlog(index)" style="padding: 3px 0" type="text" icon="el-icon-edit" v-if="token"></el-button>
                            <el-button @click="deleteBlog(index)" style="padding: 3px 0" type="text" icon="el-icon-delete" v-if="token"></el-button>
                        </div>
                    </el-col>
                </el-row>
                <div style="font-size: 0.9rem;line-height: 1.5;color: #303133;padding: 10px 0px 0px 0px">
                    <i style="font-family: '微软雅黑';"> {{item.description}} </i>
                </div>
                <div style="float: right; font-size: 0.9rem;line-height: 1.5;color: #606c71;">
                    最近更新 {{item.updateTime}}
                </div>
            </el-card>
            <div style="text-align: center">
                <el-pagination @current-change="list" background layout="prev, pager, next" :current-page.sync="query.page" :page-size="query.pageSize"
                    :total="query.pageNumber*query.pageSize">
                </el-pagination>
            </div>

        </div>

        <el-card shadow="never" style="margin-bottom: 20px;padding: 20px 0px 20px 0px;text-align: center" v-if="!blogs||blogs.length==0">
            <font style="font-size: 30px;color:#dddddd ">
                <b>还没有博客 (╯°Д°)╯︵ ┻━┻</b>
            </font>
        </el-card>
    </div>
</template>
<script>
    import { mapGetters } from 'vuex'
    import GistApi from '@/api/gist'
    export default {
        data() {
            return {
                query: {
                    page: 1,
                    pageSize: 5,
                    pageNumber: 1
                },
                loading: true,
                searchKey: "",
                blogs: [],
                selectValue: ''
            }
        },
        computed: {
            ...mapGetters([
                'token',
                'types'
            ])
        },
        mounted() {
            this.list()
        },
        methods: {
            setBlogs(response) {
                this.blogs = []
                let result = response.data
                let pageNumber = this.$util.parseHeaders(response.headers)
                if (pageNumber) {
                    this.query.pageNumber = pageNumber
                }
                for (let i = 0; i < result.length; i++) {
                    for (let key in result[i].files) {
                        let data = {}
                        data['title'] = key
                        data['url'] = result[i].files[key]
                        //data['description'] = result[i]['description']
                        let description = result[i]['description']
                        data['description'] = description.split('||')[1]
                        data['type'] = description.split('||')[0].split('>')[0]
                        data['tags'] = description.split('||')[0].split('>')[1].split('&')
                        data['id'] = result[i]['id']
                        data['createTime'] = this.$util.utcToLocal(result[i]['created_at'])
                        data['updateTime'] = this.$util.utcToLocal(result[i]['updated_at'])
                        data['hide'] = false
                        this.blogs.push(data)
                        break
                    }
                }
            },
            list() {
                this.blogs = []
                this.loading = true
                GistApi.list(this.query).then((response) => {
                    this.setBlogs(response)
                }).then(() => this.loading = false)
            },
            select(value) {
                this.searchKey = ''
                this.loading = true
                GistApi.list({}).then((response) => {
                    this.setBlogs(response)
                    for (let i = 0; i < this.blogs.length; i++) {
                        this.blogs[i].hide = this.blogs[i].type != value
                    }
                }).then(() => this.loading = false)
            },
            search() {
                this.selectValue = ''
                this.loading = true
                GistApi.list({}).then((response) => {
                    this.setBlogs(response)
                    for (let i = 0; i < this.blogs.length; i++) {
                        this.blogs[i].hide = this.blogs[i].title.indexOf(this.searchKey) < 0
                    }
                }).then(() => this.loading = false)
            },
            editBlog(index) {
                if (!this.token) {
                    this.$message({
                        message: '请绑定有效的Token',
                        type: 'warning'
                    })
                    return
                }
                this.$router.push('/user/blog/edit/' + this.blogs[index].id)
            },
            deleteBlog(index) {
                this.$confirm('是否永久删除该博客?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    let blog = this.blogs[index]
                    GistApi.delete(blog.id).then((result) => {
                        this.$message({
                            message: '删除成功',
                            type: 'success'
                        })
                        this.blogs.splice(index, 1)
                    })
                })
            },
            goAdd() {
                if (!this.token) {
                    this.$message({
                        message: '请绑定有效的Token',
                        type: 'warning'
                    })
                    return
                }
                this.$router.push('/user/blog/add')
            },
            goDetails(id) {
                this.$router.push("/user/blog/details/" + id)
            }
        }
    }
</script>

<style lang="less">
    .blog-main{
        .el-card{
            >div:nth-child(2){
                padding: 10px 20px;
            }
        }
    }
</style>


